import React, { useState } from 'react';
import Tabbar from './components/Tabbar';

const WebApp = function () {
  const [tab, setTab] = useState('home');
  return (
    <div className='web-app'>
      <h1>hello webapp</h1>
      <Tabbar tab={tab} onTab={(tab) => setTab(tab)} />
    </div>
  );
};
export default WebApp;
