import React from 'react';
import { TabBar, Badge } from 'antd-mobile';
import {
  AppOutline,
  UnorderedListOutline,

} from 'antd-mobile-icons';
import { func, string } from 'prop-types';

const onStyle = {
  color: 'bule',
};
const MyTabBar = function (props) {
  const { tab, onTab } = props;
  const tabs = [
    {
      key: 'home',
      title: '',
      icon: <AppOutline />,
      onIcon: <AppOutline style={onStyle} />,
      badge: Badge.dot,
    },
    {
      key: 'todo',
      title: '',
      icon: <UnorderedListOutline />,
      onIcon: <UnorderedListOutline style={onStyle} />,
      badge: '5',
    },
  ];
  return (
    <TabBar>
      {
        tabs.map((item) => (
          <TabBar.Item
            key={item.key}
            icon={item.icon}
            title={item.title}
            badge={item.badge}
            selected={item.key === tab}
            selectedIcon={item.onIcon}
            onPress={() => onTab(item.key)}
          />
        ))
      }
    </TabBar>
  );
};
MyTabBar.propTypes = {
  tab: string.isRequired,
  onTab: func.isRequired,
};
export default MyTabBar;
